@import url("https://fonts.googleapis.com/css2?family=Philosopher&display=swap");

html {
  scroll-behavior: smooth;
}

:target::before {
  content: "";
  display: block;
  height: 50px; /* Высота вашего фиксированного header */
  margin: -50px 0 0; /* Отрицательный margin равный высоте header */
}

body {
  margin: 0;
  padding: 0;
  font-family: "Philosopher", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(229, 238, 249);

  /* min-height: 100vh;
    background: no-repeat url("../public/bg1.jpeg");
  background-size: cover; */
}

main img,
video {
  border-radius: 10px;
  margin-bottom: 5%;
  -webkit-box-shadow: 0px 0px 6px 2px rgba(71, 155, 241, 0.7);
  -moz-box-shadow: 0px 0px 6px 2px rgba(71, 155, 241, 0.7);
  box-shadow: 0px 0px 6px 2px rgba(71, 155, 241, 0.7);
}

h2 {
  border-bottom: thick double #479bf1;
  margin: 2% auto;
  display: table;
  padding-left: 2%;
  padding-right: 2%;
}

h1 {
  font-size: 200%;
}


h1,
h2,
h3 {
  text-align: center;
}

a {
  color: inherit;
  text-decoration: none;
}

p {
  text-indent: 25px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

iframe {
  border: 0;
}

.container {
  width: 80%;
  margin: auto;
}

section {
  margin-bottom: 60px;
}