header {
    border-bottom: thick double #479bf1;
    display: flex;
    width: 100%;
    border-radius: 0 0 20px 20px;
    background-color: #303036;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 99;
    color: whitesmoke;
}

.main-menu {
    display: flex;
    width: 80%;
    margin: auto;
}

.logo {
    height: 60px;
    margin: 0;
    border: none;
}

.menu {
    margin-left: auto;
    display: flex;
    align-items: center;
}

.menu a {
    margin-left: 20px;
}
